import { Component } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { LoggedInUserService } from "src/app/services/logged-in-user.service";
import { MatSnackBar } from "@angular/material";

@Component({
  selector: "gbms-login",
  templateUrl: "./gbms-login.component.html",
  styleUrls: ["./gbms-login.component.css"],
  providers: [MatSnackBar]
})
export class GbmsLoginComponent {
  profileFound: boolean;
  loggedInUser: IUserProfile;

  constructor(private snackBar: MatSnackBar) {
    this.profileFound = false;
  }

  checkIfUserExists(username: string, password: string): void {
    if (!username || !password) {
      this.snackBar.open(
        "Both Username and Password are Required to Login!",
        "",
        {
          duration: 3000
        }
      );
      return;
    }

    let loginService = new LoggedInUserService();
    this.loggedInUser = loginService.checkIfUserExists(username, password);
    this.profileFound = loginService.getProfileFound();

    if (!this.profileFound)
      this.snackBar.open("Incorrect Login Credentials, Please Try Again!", "", {
        duration: 3000
      });
  }
}
