import { Component, Input, OnInit } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { IGame } from "src/app/interfaces/IGame";
import { MatSnackBar } from "@angular/material";

@Component({
  selector: "gbms-searched-user",
  templateUrl: "./gbms-searched-user.component.html",
  styleUrls: ["./gbms-searched-user.component.css"]
})
export class GbmsSearchedUserComponent implements OnInit {
  @Input() searchedUser: IUserProfile;
  @Input() loggedInUser: IUserProfile;
  userIsFriend: boolean;
  userBlocked: boolean;
  gotoMyProfile: boolean;

  constructor(private snackBar: MatSnackBar) {
    this.userIsFriend = false;
    this.userBlocked = false;
    this.gotoMyProfile = false;
  }

  ngOnInit() {
    for (let userId of this.loggedInUser.blockedUsers) {
      if (userId === this.searchedUser.userID) this.userBlocked = true;
    }

    for (let userId of this.loggedInUser.friendsList) {
      if (userId === this.searchedUser.userID) this.userIsFriend = true;
    }
  }

  onlineStatus(): string {
    if (this.searchedUser.onlineStatus) return "Online";
    return "Offline";
  }

  inGameStatus(): string {
    if (this.searchedUser.inMatchStatus) return "Currently Playing a Match";
    return "Sitting Idle";
  }

  overallStanding(): string {
    if (this.searchedUser.profileStanding) return "In Good Standing!";
    return "Player Is Permanently Banned From Playing Any Game!";
  }

  gameStanding(game: IGame): string {
    if (game.playerGamesStanding) return "In Good Standing!";
    return "Player Is Permanently Banned From Playing This Game!";
  }

  addRemoveFreind(): void {
    if (!this.userIsFriend) {
      this.snackBar.open("Friend Request Sent!", "", {
        duration: 3000
      });

      this.loggedInUser.friendsList.push(this.searchedUser.userID);
      this.searchedUser.friendsList.push(this.loggedInUser.userID);

      this.userIsFriend = true;
    } else {
      this.snackBar.open("Friend Removed!", "", {
        duration: 3000
      });

      this.loggedInUser.friendsList.splice(
        this.loggedInUser.friendsList.indexOf(this.searchedUser.userID),
        1
      );

      this.searchedUser.friendsList.splice(
        this.searchedUser.friendsList.indexOf(this.loggedInUser.userID),
        1
      );

      this.userIsFriend = false;
    }
  }

  blockOrUnblockUser() {
    if (!this.userBlocked) {
      this.snackBar.open("User Blocked!", "", {
        duration: 3000
      });

      this.loggedInUser.blockedUsers.push(this.searchedUser.userID);
      this.userBlocked = true;
    } else {
      this.snackBar.open("User Un-Blocked!", "", {
        duration: 3000
      });

      this.loggedInUser.blockedUsers.splice(
        this.loggedInUser.blockedUsers.indexOf(this.searchedUser.userID),
        1
      );

      this.userBlocked = false;
    }
  }

  goBackToMyProfile(): void {
    this.gotoMyProfile = true;
  }
}
