import { Component, OnInit, Input } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { IGame } from "src/app/interfaces/IGame";
import { GetProfileService } from "src/app/services/get-profile.service";
import { MatCheckboxChange } from "@angular/material";

@Component({
  selector: "gbms-invite-friends",
  templateUrl: "./gbms-invite-friends.component.html",
  styleUrls: ["./gbms-invite-friends.component.css"]
})
export class GbmsInviteFriendsComponent implements OnInit {
  @Input() loggedInUser: IUserProfile;
  allProfiles: IUserProfile[];
  @Input() selectedGame: IGame;
  friendUsernames: string[] = [];
  selectedFriends: Set<string>;
  @Input() selectedFriendsProfiles: IUserProfile[];
  friendsFoundFlag: boolean = false;
  gotoMyProfileFlag: boolean = false;
  allFriendsSelectedFlag: boolean;

  constructor(service: GetProfileService) {
    this.allProfiles = service.getUserProfiles();
    this.selectedFriendsProfiles = [];
    this.allFriendsSelectedFlag = false;
  }

  ngOnInit() {
    this.gotoMyProfileFlag = false;
    this.friendsFoundFlag = false;
    this.selectedFriends = new Set([]);
    this.findFriendsToInvite();
  }

  findFriendsToInvite(): void {
    this.friendUsernames = [];
    //selecting the friends that have the game and are available
    for (let friend of this.loggedInUser.friendsList) {
      let prof = this.allProfiles[friend - 1];
      if (prof.profileStanding && prof.onlineStatus && !prof.inMatchStatus) {
        for (let game of prof.gamesOwned) {
          if (
            this.selectedGame.gameName === game.gameName &&
            game.playerGamesStanding &&
            game.isLookingForMatch
          ) {
            this.friendUsernames.push(prof.username);
            break;
          }
        }
      } else {
        console.log("not a match" + prof);
      }
    }
    if (this.friendUsernames.length > 0) {
      this.friendsFoundFlag = true;
    }
  }

  getSelectedFriends(user: string, event: MatCheckboxChange) {
    if (event.checked) this.selectedFriends.add(user);
    else this.selectedFriends.delete(user);
  }

  inviteUninviteAllFriends() {
    this.allFriendsSelectedFlag = !this.allFriendsSelectedFlag;
    if (this.allFriendsSelectedFlag)
      for (let username of this.friendUsernames)
        this.selectedFriends.add(username);
    else this.selectedFriends.clear();
  }

  gotoMyProfile(): void {
    this.gotoMyProfileFlag = true;
    this.selectedFriendsProfiles = [];
    for (let prof of this.allProfiles)
      if (this.selectedFriends.has(prof.username))
        this.selectedFriendsProfiles.push(prof);
  }
}
