import { Injectable } from "@angular/core";
import { Profiles } from "src/app/dataset/profile-objects";
import { IUserProfile } from "../interfaces/IUserProfile";

@Injectable({
  providedIn: "root"
})
export class LoggedInUserService {
  loggedInUser: IUserProfile;
  profileFound: boolean;

  checkIfUserExists(username: string, password: string): IUserProfile {
    for (let profile of Profiles) {
      if (username === profile.username) {
        if (password === profile.password) {
          this.profileFound = true;
          profile.onlineStatus = true;
          this.loggedInUser = profile;
        }
      }
    }

    return this.loggedInUser;
  }

  getProfileFound(): boolean {
    return this.profileFound;
  }
}
