import { Component, OnInit, Input } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { IFriendsGroup } from "src/app/interfaces/IFriendsGroup";

@Component({
  selector: "gbms-select-group",
  templateUrl: "./gbms-select-group.component.html",
  styleUrls: ["./gbms-select-group.component.css"]
})
export class GbmsSelectGroupComponent implements OnInit {
  @Input() loggedInUser: IUserProfile;
  @Input() selectedGroup: IFriendsGroup;
  gotoGroups: boolean;
  anyGroupMembers: boolean;

  constructor() {
    this.gotoGroups = false;
    this.anyGroupMembers = false;
  }

  ngOnInit() {
    console.log(this.selectedGroup);
    let groupIndex = this.loggedInUser.friendsGroups.indexOf(
      this.selectedGroup
    );
    this.selectedGroup = this.loggedInUser.friendsGroups[groupIndex];
    console.log("new");
    console.log(this.selectedGroup);
    if (this.selectedGroup.groupMembers.length > 0) this.anyGroupMembers = true;
    else this.anyGroupMembers = false;
  }

  removeMember(groupMember: number): void {
    this.selectedGroup.groupMembers.splice(
      this.selectedGroup.groupMembers.indexOf(groupMember),
      1
    );
    if (this.selectedGroup.groupMembers.length == 0)
      this.anyGroupMembers = false;
  }

  addMemberToGroup(newMember: number): void {
    this.selectedGroup.groupMembers.push(newMember);
    this.anyGroupMembers = true;
  }

  removeAllMembers(): void {
    this.selectedGroup.groupMembers = [];
    this.anyGroupMembers = false;
  }

  goBackToGroups(): void {
    this.gotoGroups = true;
  }
}
