import { Component, OnInit, Input } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { IGame } from "src/app/interfaces/IGame";
import { GetProfileService } from "src/app/services/get-profile.service";

@Component({
  selector: "gbms-matchmaking",
  templateUrl: "./gbms-matchmaking.component.html",
  styleUrls: ["./gbms-matchmaking.component.css"]
})
export class GbmsMatchmakingComponent implements OnInit {
  @Input() loggedInUser: IUserProfile;
  allProfiles: IUserProfile[];
  @Input() selectedGame: IGame;
  @Input() selectedFriendsProfiles: IUserProfile[];
  matchedProfiles: IUserProfile[];
  matchFoundFlag: boolean;
  gotoMyProfile: boolean;
  matchInProgress: boolean;
  matchAcceptedProfiles = new Set<IUserProfile>();

  constructor(service: GetProfileService) {
    this.allProfiles = service.getUserProfiles();
    this.matchFoundFlag = false;
    this.gotoMyProfile = false;
    this.matchInProgress = false;
  }

  ngOnInit(): void {
    this.findMatchingProfiles();
  }

  findMatchingProfiles(): void {
    let availProf: IUserProfile[] = [];
    let blockedUserIds = new Set(this.loggedInUser.blockedUsers);
    let i: number = 0;
    //selecting the profiles that are available to play
    for (let prof of this.allProfiles) {
      if (
        !blockedUserIds.has(prof.userID) &&
        prof.profileStanding &&
        prof.onlineStatus &&
        !prof.inMatchStatus
      ) {
        for (let game of prof.gamesOwned) {
          if (
            this.selectedGame.gameName === game.gameName &&
            game.playerGamesStanding &&
            game.isLookingForMatch
          ) {
            availProf.push(prof);
            i++;
            break;
          }
        }
      }
    }

    //finding the best fit profiles
    let selectedProf = new Set(this.selectedFriendsProfiles);
    selectedProf.add(this.loggedInUser);

    if (availProf.length < 1) {
      return;
    }

    this.matchFoundFlag = true;
    let mod = 0;
    let modP = 0;

    for (i = 0; i < 10 && selectedProf.size < 51; i++) {
      mod = mod + 2;
      modP = modP + 400;
      let stillAvailProf: IUserProfile[];
      stillAvailProf = [];

      /* check if behaviourIndex is compatible */
      for (let profile of availProf) {
        if (
          profile.behaviorIndex <= this.loggedInUser.behaviorIndex + mod &&
          profile.behaviorIndex >= this.loggedInUser.behaviorIndex - mod
        ) {
          for (let game of profile.gamesOwned) {
            /* for the selected game, check if other characteristics match */
            if (game.gameName === this.selectedGame.gameName) {
              if (
                game.playerSkillLevel <=
                  this.selectedGame.playerSkillLevel + mod &&
                game.playerSkillLevel >=
                  this.selectedGame.playerSkillLevel - mod
              )
                if (
                  game.hoursPlayed <= this.selectedGame.hoursPlayed + modP &&
                  game.hoursPlayed >= this.selectedGame.hoursPlayed - modP
                ) {
                  if (!selectedProf.has(profile)) selectedProf.add(profile);
                } else stillAvailProf.push(profile);
            }
          } // end of for
        } // end of behavior match
      } // end of foreach profile
      availProf = stillAvailProf; //try again to find matches
    }
    this.matchedProfiles = Array.from(selectedProf);
    return;
  }

  acceptMatch(): void {
    this.loggedInUser.inMatchStatus = true;

    for (let i = 0; i < 10; i++) {
      console.log("test");
      if (this.matchedProfiles[i] !== this.loggedInUser) {
        setTimeout(() => {
          this.matchedProfiles[i].inMatchStatus = true;
          this.matchAcceptedProfiles.add(this.matchedProfiles[i]);
        }, Math.random() * 5000);
      }
    }

    setTimeout(() => {
      this.matchInProgress = true;
    }, 8000);
  }

  rejectMatch(): void {
    this.loggedInUser.inMatchStatus = false;
    this.matchedProfiles.splice(
      this.matchedProfiles.indexOf(this.loggedInUser),
      1
    );
    this.goBackToMyProfile();
  }

  autoAcceptRejectPlayers(): void {
    for (let i = 0; i < 10; i++) {
      if (!this.matchedProfiles[i].inMatchStatus) {
        if (Math.floor(Math.random() * 10) > 5)
          this.matchedProfiles[i].inMatchStatus = true;
        else {
          console.log("removing " + this.matchedProfiles[i]);
          this.matchedProfiles.splice(
            this.matchedProfiles.indexOf(this.matchedProfiles[i]),
            1
          );
        }
      }
    }
  }

  goBackToMyProfile(): void {
    this.gotoMyProfile = true;
  }
}
