import { Component, OnInit, Input } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { IFriendsGroup } from "src/app/interfaces/IFriendsGroup";
import { GetProfileService } from "src/app/services/get-profile.service";

@Component({
  selector: "gbms-create-group",
  templateUrl: "./gbms-create-group.component.html",
  styleUrls: ["./gbms-create-group.component.css"]
})
export class GbmsCreateGroupComponent implements OnInit {
  @Input() loggedInUser: IUserProfile;
  gotoMyProfile: boolean;
  newGroup: IFriendsGroup;
  newGroupAddedFlag: boolean;
  gotoGroupsFlag: boolean;
  gotoMyProfileFlag: boolean;

  constructor(service: GetProfileService) {
    this.gotoMyProfile = false;
    this.newGroupAddedFlag = false;
    this.newGroup = null;
    this.gotoGroupsFlag = false;
    this.gotoMyProfileFlag = false;
  }

  ngOnInit() {}

  createGroup(groupName: string): void {
    let tempGroupID: number;
    for (let group of this.loggedInUser.friendsGroups) {
      if (group.groupName == groupName) {
        console.log("Duplicate group name");
        return;
      }
      tempGroupID = group.groupID;
    }
    const tempGroup: IFriendsGroup = {
      groupID: tempGroupID + 1,
      groupName: groupName,
      groupMembers: []
    };
    this.newGroup = tempGroup;
    console.log("new group");
    console.log(this.newGroup);
    this.loggedInUser.friendsGroups.push(this.newGroup);
    this.newGroupAddedFlag = true;
  }

  addMemberToGroup(userID: number): void {
    if (this.newGroup.groupMembers.includes(userID))
      console.log("already in group");
    else this.newGroup.groupMembers.push(userID);
    console.log(this.newGroup);
  }

  goBackToGroups(): void {
    this.gotoGroupsFlag = true;
  }

  goBackToMyProfile(): void {
    this.gotoMyProfileFlag = true;
  }
}
