import { Component, Input, OnInit } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { IGame } from "src/app/interfaces/IGame";
import { FormControl } from "@angular/forms";
import { Observable } from "rxjs";
import { map, startWith } from "rxjs/operators";
import { GetProfileService } from "src/app/services/get-profile.service";

@Component({
  selector: "gbms-my-profile",
  templateUrl: "./gbms-my-profile.component.html",
  styleUrls: ["./gbms-my-profile.component.css"]
})
export class GbmsMyProfileComponent implements OnInit {
  @Input() loggedInUser: IUserProfile;
  @Input() selectedGame: IGame;
  @Input() selectedFriendsProfiles: IUserProfile[];
  allProfiles: IUserProfile[];
  selectedGameFlag: boolean;
  inviteFriendsFlag: boolean;
  myControl = new FormControl();
  userIsSearched: boolean;
  options: string[];
  filteredOptions: Observable<string[]>;
  searchedUser: IUserProfile;
  blockedUserTogler: boolean;
  friendsListToggler: boolean;
  groupsToggler: boolean;
  friendsSelectedFlag: boolean;

  constructor(service: GetProfileService) {
    this.allProfiles = service.getUserProfiles();
    this.userIsSearched = false;
    this.options = [];
    this.selectedGameFlag = false;
    this.inviteFriendsFlag = false;
    this.blockedUserTogler = false;
    this.friendsListToggler = false;
    this.groupsToggler = false;
    this.friendsSelectedFlag = false;
  }

  ngOnInit() {
    let firendsList = new Set();
    for (let id of this.loggedInUser.friendsList) {
      firendsList.add(id);
    }

    for (let profile of this.allProfiles) {
      if (
        this.loggedInUser.userID !== profile.userID &&
        !firendsList.has(profile.userID)
      )
        this.options.push(profile.username);
    }

    this.filteredOptions = this.myControl.valueChanges.pipe(
      startWith(""),
      map(value => this._filter(value))
    );

    if (this.selectedFriendsProfiles)
      if (this.selectedFriendsProfiles.length > 0)
        this.friendsSelectedFlag = true;
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.options
      .filter(option => option.toLowerCase().includes(filterValue))
      .slice(0, 5);
  }

  onlineStatus(): string {
    if (this.loggedInUser.onlineStatus) return "Online";
    return "Offline";
  }

  inGameStatus(): string {
    if (this.loggedInUser.inMatchStatus) return "Currently Playing a Match";
    return "Sitting Idle";
  }

  overallStanding(): string {
    if (this.loggedInUser.profileStanding) return "In Good Standing!";
    return "Player Is Permanently Banned From Playing Any Game!";
  }

  gameStanding(game: IGame): string {
    if (game.playerGamesStanding) return "In Good Standing!";
    return "Player Is Permanently Banned From Playing This Game!";
  }

  setSelectedGame(game: IGame) {
    if (game !== undefined) {
      this.selectedGame = game;
    } else {
      this.inviteFriendsFlag = false;
      this.selectedGameFlag = false;
    }
  }

  selectGame(): void {
    if (this.selectedGame !== undefined) {
      this.inviteFriendsFlag = false;
      this.selectedGameFlag = true;
    }
  }

  inviteFriends(): void {
    if (this.selectedGame !== undefined) {
      this.inviteFriendsFlag = true;
    }
  }

  userSearched(searchTerm: string) {
    for (let user of this.allProfiles) {
      if (searchTerm === user.username) {
        this.searchedUser = user;
        this.userIsSearched = true;
      }
    }
  }

  showBlockedUsers(): void {
    this.blockedUserTogler = true;
  }

  showFriendsList(): void {
    this.friendsListToggler = true;
  }

  showGroups(): void {
    this.groupsToggler = true;
  }
}
