import { Component, Input } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";

@Component({
  selector: "gbms-match-in-progress",
  templateUrl: "./gbms-match-in-progress.component.html",
  styleUrls: ["./gbms-match-in-progress.component.css"]
})
export class GbmsMatchInProgressComponent {
  goToSurvey: boolean;
  @Input() loggedInUser: IUserProfile;
  @Input() matchAcceptedProfiles: Set<IUserProfile>;

  constructor() {
    this.goToSurvey = false;

    setTimeout(() => {
      this.goToSurvey = true;
    }, 5000);
  }
}
