import { Component, OnInit, Input } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { GetProfileService } from "src/app/services/get-profile.service";
import { IFriendsGroup } from "src/app/interfaces/IFriendsGroup";

@Component({
  selector: "gbms-groups",
  templateUrl: "./gbms-groups.component.html",
  styleUrls: ["./gbms-groups.component.css"]
})
export class GbmsGroupsComponent implements OnInit {
  @Input() loggedInUser: IUserProfile;
  allUserProfiles: IUserProfile[];
  anyGroups: boolean;
  gotoMyProfileFlag: boolean;
  selectedGroupFlag: boolean;
  selectedGroup: IFriendsGroup;
  gotoCreateGroupFlag: boolean;

  constructor(service: GetProfileService) {
    this.allUserProfiles = service.getUserProfiles();
    this.gotoMyProfileFlag = false;
    this.gotoCreateGroupFlag = false;
    this.selectedGroupFlag = false;
    this.selectedGroup = null;
    this.anyGroups = false;
  }

  ngOnInit() {

    if (this.loggedInUser.friendsGroups.length > 0) this.anyGroups = true;

  }


  goBackToMyProfile(): void {
    this.gotoMyProfileFlag = true;
  }

  selectGroup(group: IFriendsGroup): void {
    this.selectedGroup = group;
    console.log(this.selectedGroup);
    this.selectedGroupFlag = true;
  }

  deleteGroup(delGroup: IFriendsGroup): void {
    console.log(delGroup);
    for (let group of this.loggedInUser.friendsGroups) {
      if (group.groupID == delGroup.groupID) {
          this.loggedInUser.friendsGroups.splice(this.loggedInUser.friendsGroups.indexOf(group), 1) 
          console.log("remove Group");
          console.log(delGroup);
        }
      }
      if (this.loggedInUser.friendsGroups.length == 0) this.anyGroups = false;
    }
  

  createGroup(): void {
    this.gotoCreateGroupFlag = true;
  }

}
