import { Component, OnInit, Input, AfterViewInit } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { GetProfileService } from "src/app/services/get-profile.service";

@Component({
  selector: "gbms-block",
  templateUrl: "./gbms-block.component.html",
  styleUrls: ["./gbms-block.component.css"]
})
export class GbmsBlockComponent implements OnInit {
  @Input() loggedInUser: IUserProfile;
  allUserProfiles: IUserProfile[];
  blockedUsers: IUserProfile[];
  anyUserBlocked: boolean;
  gotoMyProfile: boolean;

  constructor(service: GetProfileService) {
    this.allUserProfiles = service.getUserProfiles();
    this.blockedUsers = [];
    this.gotoMyProfile = false;
  }

  ngOnInit() {
    for (let userID of this.loggedInUser.blockedUsers) {
      for (let user of this.allUserProfiles) {
        if (userID === user.userID) this.blockedUsers.push(user);
      }
    }

    if (this.loggedInUser.blockedUsers.length > 0) this.anyUserBlocked = true;
  }

  unblockUser(user: IUserProfile): void {
    this.loggedInUser.blockedUsers.splice(
      this.loggedInUser.blockedUsers.indexOf(user.userID),
      1
    );

    this.blockedUsers.splice(this.blockedUsers.indexOf(user), 1);

    if (this.blockedUsers.length === 0) this.anyUserBlocked = false;
  }

  unblockAll(): void {
    this.loggedInUser.blockedUsers = [];
    this.blockedUsers = [];
    this.anyUserBlocked = false;
  }

  goBackToMyProfile(): void {
    this.gotoMyProfile = true;
  }
}
