import { Component, OnInit, Input } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { GetProfileService } from "src/app/services/get-profile.service";

@Component({
  selector: "gbms-friends-list",
  templateUrl: "./gbms-friends-list.component.html",
  styleUrls: ["./gbms-friends-list.component.css"]
})
export class GbmsFriendsListComponent implements OnInit {
  @Input() loggedInUser: IUserProfile;
  allUserProfiles: IUserProfile[];
  friends: IUserProfile[];
  anyFriends: boolean;
  gotoMyProfile: boolean;

  constructor(service: GetProfileService) {
    this.allUserProfiles = service.getUserProfiles();
    this.friends = [];
    this.gotoMyProfile = false;
  }

  ngOnInit() {
    for (let userID of this.loggedInUser.friendsList) {
      for (let user of this.allUserProfiles) {
        if (userID === user.userID) this.friends.push(user);
      }
    }

    console.log(this.loggedInUser.friendsList);
    if (this.loggedInUser.friendsList.length > 0) this.anyFriends = true;
  }

  removeFriend(user: IUserProfile): void {
    this.loggedInUser.friendsList.splice(
      this.loggedInUser.friendsList.indexOf(user.userID),
      1
    );

    this.friends.splice(this.friends.indexOf(user), 1);

    if (this.friends.length === 0) this.anyFriends = false;
  }

  removeAllFriends(): void {
    this.loggedInUser.friendsList = [];
    this.friends = [];
    this.anyFriends = false;
  }

  goBackToMyProfile(): void {
    this.gotoMyProfile = true;
  }
}
