import { Component, Input } from "@angular/core";
import { IUserProfile } from "src/app/interfaces/IUserProfile";
import { MatSnackBar } from "@angular/material";

@Component({
  selector: "gbms-survey",
  templateUrl: "./gbms-survey.component.html",
  styleUrls: ["./gbms-survey.component.css"]
})
export class GbmsSurveyComponent {
  @Input() loggedInUser: IUserProfile;
  @Input() matchAcceptedProfiles: IUserProfile[];
  ratings: string[];
  yesNo: string[];
  gotoMyProfile: boolean;

  constructor(public snackBar: MatSnackBar) {
    this.ratings = ["Excellent", "Good", "Average", "Poor", "Very Poor"];
    this.yesNo = ["Yes", "No"];
    this.gotoMyProfile = false;
  }

  updateSurveyInfo(): void {
    this.snackBar.open("Thankyou! for submitting the survey", "", {
      duration: 3000
    });

    this.loggedInUser.inMatchStatus = false;
    this.gotoMyProfile = true;
  }

  goBackToMyProfile(): void {
    this.loggedInUser.inMatchStatus = false;
    this.gotoMyProfile = true;
  }
}
